import HeadCustom from '../components/head-custom/head-custom.component';
import styles from './about.module.scss';

function About({ publicUrl }) {
  return (<>
    <HeadCustom
        title='about'
        description='programming, woodworking, and more'
        keywords='blog, react, woodworking, programming, about, jwango'
        baseUrl={publicUrl}
        relUrl='/about'>
    </HeadCustom>
    <article className={styles['about']}>
        <header className={styles['about--wide']}>
            <h2>Josh Wang</h2>
            <span className={styles['about__profile']}>
                <img src='/profile_balloon.jpg' alt='profile with balloon face' width='96' height='96' className='img__inline'/>
                <p>Professional software engineer. Amateur woodworker. Occasional thrower of round objects.</p>
                <p>B.S. Computer Science, Georgia Institute of Technology</p>
            </span>
        </header>
        
        <section className={styles['about__item']}>
            <h3>Outward</h3>
            <ul>
                <li>Frontend frameworks</li>
                <li>Computer-aided design</li>
                <li>Woodworking</li>
                <li>3D printing</li>
                <li>Sustainability</li>
                <li>Basketball</li>
                <li>Ultimate frisbee and disc golf</li>
            </ul>
        </section>

        <section className={styles['about__item']}>
            <h3>Inward</h3>
            <details><summary>Click me?</summary>Don't know what you expected here, but try reading some of my blog posts!</details>
        </section>

        <section className={styles['about__item']}>
            <h3>Forward</h3>
            <p>Reach me at <a href="mailto:joshua.wang.h@gmail.com">joshua.wang.h@gmail.com</a>. </p>
        </section>
    </article>
</>);
}

export async function getStaticProps() {
    return { props: { publicUrl: process.env.PUBLIC_URL } }
}

export default About;